import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import Popup from '../Popup/Popup';
import Button from '../Button/Button';
import { openResultPopup, closeWinPopup } from '../../redux/appSlice';
import { addToResults } from '../../redux/resultsSlice';
import { msToTime } from '../../utils/msToTime';
import './WinPopup.css';

function WinPopup() {
  const [name, setName] = React.useState('');

  const isOpenWinPopup = useSelector((state) => state.app.isOpenWinPopup);
  const pastTime = useSelector((state) => state.timer.pastTime);
  const dispatch = useDispatch();

  function handleClick() {
    if (name.trim()) {
      dispatch(addToResults({ name, time: pastTime }));
      closePopup();
      dispatch(openResultPopup());
      setName('');
    }
  }

  function closePopup() {
    dispatch(closeWinPopup());
  }

  return (
    <Popup title="–ü–æ–∑–¥—Ä–∞–≤–ª—è—é, –≤—ã –ø–æ–±–µ–¥–∏–ª–∏" isOpen={isOpenWinPopup} close={closePopup}>
      <p className="popup__text">–í–∞—à —Ä–µ–∑—É–ª—å—Ç–∞—Ç: {msToTime(pastTime)}</p>
      <input
        value={name}
        onChange={(e) => setName(e.target.value)}
        className="popup__input"
        placeholder="–í–≤–µ–¥–∏—Ç–µ —Å–≤–æ–µ –∏–º—è"
        maxLength="15"
      />
      <Button title="–î–æ–±–∞–≤–∏—Ç—å –≤ —Ç–∞–±–ª–∏—Ü—É" onClick={handleClick} isSmallTitle={true} />
    </Popup>
  );
}

export default WinPopup;
